  .footer__logo {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    width: 69px;
  }
}
.footer__logo-icon {
  fill: #ffffff;
  width: 100%;
  height: 100%;
  transition: 0.4s;
}
.footer__logo-icon:hover, .footer__logo-icon:focus-within {
  transform: scale(1.3);
  transition: 0.4s;
}
